    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findMonList() {
      try {
        var param = {
          startTime: this.year + '-01-01 00:00:00',
          endTime: this.year + '-12-01 00:00:00',
          type: 'GAS'
        };

        var condtion = {};

        if (this.groupType === 'DEVICE') {
          condtion.deviceCode = this.rows.deviceCode;
        } else {
          condtion.groupId = this.rows.groupId;
        }

        if (this.shiftData !== null) {
          if (this.shiftData.length > 0) {
            condtion.shiftQueryList = [];
            for (var y = 0; y < this.shiftData.length; y++) {
              var jsonx = {
                shiftName: this.shiftOptions[this.shiftData[y]].name,
                shiftStart: this.shiftOptions[this.shiftData[y]].shiftStart,
                shiftEnd: this.shiftOptions[this.shiftData[y]].shiftEnd
              };
              condtion.shiftQueryList.push(jsonx);
            }
          }
        }

        this.tableMon.tableLoading = true;
        const res = await getHistoricalMonthShiftEnergy(param, condtion);
        this.tableMon.tableLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table

          this.tableMon.tableColumn = [
            {
              field: 'dateTime',
              title: '月份',
              align: 'center'
            }
          ];

          this.tableMon.tableData = [];
          for (var i = 0; i < res.rows.length; i++) {
            this.tableMon.tableColumn.push({
              field: 'xValue' + '_' + i,
              title: res.rows[i].shiftName + ENERGY.GAS_CONSUMPTION,
              align: 'center'
            });
            // console.log('月份数据', res.rows[i].xyVoList);
            for (var x = 0; x < res.rows[i].xyVoList.length; x++) {
              var datax = res.rows[i].xyVoList[x];
              if (i === 0) {
                var json = {};
                json.dateTime = datax.x.substring(0, 7);
                json['xValue' + '_' + i] = Number(datax.y.toFixed(2));
                this.tableMon.tableData.push(json);
              } else {
                this.tableMon.tableData[x]['xValue' + '_' + i] = Number(datax.y.toFixed(2));
              }
            }
          }

          // 将总条数写入table分页配置
          this.tableMon.tablePage.total = 12;
          // 加载动画消失
          // this.tableMon.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:展示车间7日奇书图形
     * @param {type}
     * @return:
     * @author: CH
     */
    async showMonEcharts() {
      // this.drawMonthEcharts([], []);
      try {
        var param = {
          startTime: this.year + '-01-01 00:00:00',
          endTime: this.year + '-12-01 00:00:00',
          type: 'GAS'
        };

        var condtion = {};

        if (this.groupType === 'DEVICE') {
          condtion.deviceCode = this.rows.deviceCode;
        } else {
          condtion.groupId = this.rows.groupId;
        }

        if (this.shiftData !== null) {
          if (this.shiftData.length > 0) {
            condtion.shiftQueryList = [];
            for (var y = 0; y < this.shiftData.length; y++) {
              var jsonx = {
                shiftName: this.shiftOptions[this.shiftData[y]].name,
                shiftStart: this.shiftOptions[this.shiftData[y]].shiftStart,
                shiftEnd: this.shiftOptions[this.shiftData[y]].shiftEnd
              };
              condtion.shiftQueryList.push(jsonx);
            }
          }
        }

        this.tableLoading = true;
        const res = await getHistoricalMonthShiftEnergy(param, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          var data = res.rows;

          var series = [];
          var xValue = [];

          for (var i = 0; i < data.length; i++) {
            var json = {
              name: data[i].shiftName,
              type: 'bar',
              // symbol: 'none',
              symbol: 'circle',
              symbolSize: 0.01,
              label: {
                show: true,
                position: 'top',
                color: 'auto'
              },
              itemStyle: {
                normal: {
                  color: '#1b89f7'
                }
              },
              data: []
            };
            for (var x = 0; x < data[i].xyVoList.length; x++) {
              json.data.push(Number(data[i].xyVoList[x].y.toFixed(2)));
              if (i === 0) {
                xValue.push(data[i].xyVoList[x].x.substring(5, 10));
              }
            }
            series.push(json);
          }
          this.drawMonthEcharts(xValue, series);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
