    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findHourList() {
      try {
        var param = {
          time: this.day + ' 00:00:00',
          type: 'GAS'
        };

        if (this.groupType === 'DEVICE') {
          param.deviceCode = this.rows.deviceCode;
        } else if (this.groupType === 'WORK_SHOP') {
          param.groupId = this.rows.groupId;
          param.groupType = 2;
        } else if (this.groupType === 'PRODUCTION_LINE') {
          param.groupId = this.rows.groupId;
          param.groupType = 3;
        }

        this.tableHour.tableLoading = true;
        const res = await GetHistoricaHourEnergy(param);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          // this.tableDatas = res.rows;
          this.tableHour.tableData = [];
          for (var i in res.rows.data.dateTime) {
            var json = {
              dateTime: res.rows.data.dateTime[i],
              dayConsumption: res.rows.data.dayConsumption[i]
            };
            this.tableHour.tableData.push(json);
          }
          // 将总条数写入table分页配置
          this.tableHour.tablePage.total = 24;
          // 加载动画消失
          this.tableHour.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:展示车间7日奇书图形
     * @param {type}
     * @return:
     * @author: CH
     */
    async showHourEcharts() {
      try {
        this.tableLoading = true;
        var param = {
          time: this.day + ' 00:00:00',
          type: 'GAS'
        };

        if (this.groupType === 'DEVICE') {
          param.deviceCode = this.rows.deviceCode;
        } else if (this.groupType === 'WORK_SHOP') {
          param.groupId = this.rows.groupId;
          param.groupType = 2;
        } else if (this.groupType === 'PRODUCTION_LINE') {
          param.groupId = this.rows.groupId;
          param.groupType = 3;
        }
        const res = await GetHistoricaHourEnergy(param);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          var data = res.rows.data;
          // 基于准备好的dom，初始化echarts实例
          var myChart = this.$echarts.init(
            document.getElementById('hourEchart')
          );
          // 绘制图表
          myChart.setOption({
            // title: {
            //   text: ENERGY.SEVEN_DAYS_ECHARTS,
            //   x: 'center',
            //   y: 'top'
            // },
            tooltip: {
              trigger: 'axis'
            },
            xAxis: {
              type: 'category',
              splitLine: { show: false },
              boundaryGap: true,
              data: data.dateTime,
              axisLabel: {
                show: true
              }
            },
            dataZoom: [
              {
                type: 'slider',
                show: true,
                xAxisIndex: [0],
                left: '9%',
                bottom: 10,
                start: 10,
                end: 90
              }
            ],
            yAxis: {
              type: 'value',
              scale: true,
              splitLine: { show: false }
            },
            series: [
              {
                name: ENERGY.DAY_ENERGY,
                type: 'line',
                stack: ENERGY.ALL,
                areaStyle: {},
                smooth: true,
                symbol: 'circle',
                symbolSize: 0.01,
                label: {
                  show: true,
                  position: 'top',
                  color: 'auto'
                },
                lineStyle: {
                  normal: {
                    color: '#1b89f7'
                  }
                },
                itemStyle: {
                  normal: {
                    color: '#1b89f7'
                  }
                },
                data: data.dayConsumption
              }
            ]
          });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
