    async getSystemShift() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            var index = 0;
            for (var key in jsonArr) {
              var json = {
                value: index,
                label: key + ' ' + jsonArr[key],
                name: key,
                shiftStart: jsonArr[key].split('-')[0],
                shiftEnd: jsonArr[key].split('-')[1]
              };
              ++index;
              this.shiftOptions.push(json);
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
