import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  ENERGY,
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EXPORT_NAME
} from '@/utils/constantsFile';
import {
  EnergyGetMeterDropdown
  // GetEnergyConsumptionStatistics
} from '@/api/energymonitoring';
import {
  getShiftEnergyConsumptionStatisticsLineChart
} from '@/api/energyShift';
import {
  getSearchToday
} from '@/utils/publicFunction2';
// import { normalizeComponent } from 'vue-runtime-helpers';
export default {
  name: 'echarts',
  props: {
    dateTime: {
      type: Array,
      default: () => {
        return [];
      }
    },
    shiftOptions: {
      type: Array,
      default: () => {
        return [];
      }
    },
    shiftData: Array
  },
  data() {
    return {
      ENERGY: ENERGY,
      TITLE: TITLE,
      BTN_TEXT: BTN_TEXT,
      options: [],
      startTime: '',
      endTime: '',
      selectValue: '',
      echartsHref: null,
      downloadName: null
    };
  },
  created() {
    // 调用列表查询接口
    this.findDevice();
    this.$nextTick(function() {
      this.showEcharts();
    });
  },
  mounted() {},
  methods: {
    /**
     * @description:查询数据
     * @param {type}
     * @return
     * @author: CH
     */
    search() {
      if (this.dateTime !== '') {
        this.startTime = this.dateTime[0];
        this.endTime = this.dateTime[1];
      }
      this.showEcharts();
    },
    /**
     * @description:重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.dateTime = '';
      this.selectValue = '';
      this.startTime = '';
      this.endTime = '';
      this.showEcharts();
    },
    /**
     * @description:展示图形
     * @param {type}
     * @return:
     * @author: CH
     */
    async showEcharts() {
      console.log(this.dateTime, this.shiftOptions, this.shiftData);
      this.drawEcharts([], []);
      try {
        this.tableLoading = true;

        var param = {
          type: 'GAS'
        };

        var condtion = {};

        if (this.dateTime.length > 0) {
          param.startTime = this.dateTime[0];
          param.endTime = this.dateTime[1];
        } else {
          param.startTime = getSearchToday(7) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }

        if (this.shiftData !== null) {
          if (this.shiftData.length > 0) {
            condtion.shiftQueryList = [];
            for (var y = 0; y < this.shiftData.length; y++) {
              var jsonx = {
                shiftName: this.shiftOptions[this.shiftData[y]].name,
                shiftStart: this.shiftOptions[this.shiftData[y]].shiftStart,
                shiftEnd: this.shiftOptions[this.shiftData[y]].shiftEnd
              };
              condtion.shiftQueryList.push(jsonx);
            }
          }
        }

        console.log(param, condtion);

        const res = await getShiftEnergyConsumptionStatisticsLineChart(param, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          var data = res.rows;

          var series = [];
          var xValue = [];

          for (var i = 0; i < data.length; i++) {
            var json = {
              name: data[i].shiftName,
              type: 'line',
              // symbol: 'none',
              symbol: 'circle',
              symbolSize: 0.01,
              label: {
                show: true,
                position: 'top',
                color: 'auto'
              },
              itemStyle: {
                normal: {
                  color: '#1b89f7'
                }
              },
              data: []
            };
            for (var x = 0; x < data[i].xyVoList.length; x++) {
              json.data.push(data[i].xyVoList[x].y);
              if (i === 0) {
                xValue.push(data[i].xyVoList[x].x.substring(5, 10));
              }
            }
            series.push(json);
          }
          this.drawEcharts(xValue, series);
          // this.getEchartURL();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    drawEcharts(xValue, series) {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(
        document.getElementById('showEcharts')
      );
      // 绘制图表
      myChart.setOption({
        title: {},
        legend: {
          right: 30,
          top: 12,
          itemWidth: 20,
          itemHeight: 10
        },
        grid: {
          left: '2%',
          right: '4%',
          bottom: '4%',
          top: '16%',
          containLabel: true
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          data: xValue,
          axisLabel: {
            show: true
          }
        },
        yAxis: {
          type: 'value',
          scale: true
        },
        series: series
      }, true);
    },
    /**
     * @description:查询设备
     * @param{row}
     * @return:{json}
     * @author: CH
     */
    async findDevice() {
      try {
        this.tableLoading = true;
        const res = await EnergyGetMeterDropdown(
          {
            type: 'GAS'
          },
          [
            {
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: '2' // 参数值
            }
          ]
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          this.options = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:倒入下载路径
     * @param {type}
     * @return:
     * @author: CH
     */
    getEchartURL() {
      var myChart = this.$echarts.init(document.getElementById('showEcharts'));
      var i = myChart.getDataURL({
        type: 'png',
        backgroundColor: 'white'
        // 导出的图片分辨率比例，默认为 1。
      });
      this.echartsHref = i;
      this.downloadName = EXPORT_NAME.POWER_STATISTICS;
      setTimeout(() => {
        this.$refs.export.click();
      }, 200);
    },
    /**
     * @description:下载
     * @param {type}
     * @return:
     * @author: CH
     */
    exportEchart() {
      this.getEchartURL();
      // this.$refs.export.click();
    }
  },
  components: {
    HeadOperationItem
  }
};
