// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  ENERGY,
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  PAGE_TITLE
  // EXPORT_NAME
} from '@/utils/constantsFile';
// 导入所需接口
import { getShiftGroupConsumption } from '@/api/energyShift';
// import { exportEnergyMonitor } from '@/api/export';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: {
    groupType: String,
    showDialog: {
      type: Function,
      default: null
    },
    shiftOptions: {
      type: Array,
      default: () => {
        return [];
      }
    },
    shiftData: Array
  },
  data() {
    return {
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // table是否加载
      tableLoading: false,
      // 导出数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [],
      // tableColumns: [
      //   {
      //     field: 'name',
      //     title: PAGE_TITLE.WORKSHOP_NAME,
      //     width: '',
      //     align: 'center'
      //   },

      //   {
      //     field: 'allConsumption',
      //     title: ENERGY.ALLY_POWER_CONSUMPTION,
      //     width: '',
      //     align: 'center'
      //   },
      //   {
      //     field: '',
      //     title: BTN_TEXT.OPERATION,
      //     width: '',
      //     align: 'center',
      //     slots: {
      //       default: ({ row, column, cellValue }) => {
      //         return [
      //           <div>
      //             <el-button
      //               type="text"
      //               onClick={() => this.success(row, column, false)}
      //             >
      //               {BTN_TEXT.HISTORICAL_DATA}
      //             </el-button>
      //           </div>
      //         ];
      //       }
      //     }
      //   }
      // ],
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  watch: {
    // 发现type发生变化触发事件
    groupType(val) {
      if (val !== '') {
      //   this.tableColumns[4].title = ENERGY.ALLY_POWER_CONSUMPTION;
      //   if (val === '2') {
      //     this.tableColumns[0].title = PAGE_TITLE.WORKSHOP_NAME;
      //   } else {
      //     this.tableColumns[0].title = PAGE_TITLE.PRODUCTION_LINE_NAME;
      //   }
      //   this.onTablePageChange(1, 10);
      // } else {
      //   this.groupType = '';
      //   this.tableColumns[0].title = ENERGY.DEVICENAME;
      //   this.tableColumns[4].title = ENERGY.GAS_METER_RECORD;
        this.onTablePageChange(1, 10);
      }
    }
  },
  created() {
    // 调用列表查询接口
    this.findList();
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        var param = {
          size: this.tablePage.pageSize,
          current: this.tablePage.currentPage,
          type: 'GAS',
          // groupType: 'WORK_SHOP'
          groupType: this.groupType
        };
        var condtion = {};
        if (this.shiftData !== null) {
          if (this.shiftData.length > 0) {
            condtion.shiftQueryList = [];
            for (var y = 0; y < this.shiftData.length; y++) {
              var jsonx = {
                shiftName: this.shiftOptions[this.shiftData[y]].name,
                shiftStart: this.shiftOptions[this.shiftData[y]].shiftStart,
                shiftEnd: this.shiftOptions[this.shiftData[y]].shiftEnd
              };
              condtion.shiftQueryList.push(jsonx);
            }
          }
        }

        this.tableLoading = true;
        const res = await getShiftGroupConsumption(param, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;

          this.tableColumns = [
            {
              field: 'name',
              title: PAGE_TITLE.WORKSHOP_NAME,
              width: '',
              align: 'center'
            },
            {
              title: ENERGY.DAY_GAS_CONSUMPTION,
              align: 'center',
              children: []
            },
            {
              title: ENERGY.WEEKY_GAS_CONSUMPTION,
              align: 'center',
              children: []
            },
            {
              title: ENERGY.MONTHY_GAS_CONSUMPTION,
              align: 'center',
              children: []
            },
            {
              field: 'meterValue',
              title: '表读数',
              width: '',
              align: 'center'
            },
            {
              field: '',
              title: BTN_TEXT.OPERATION,
              width: '',
              align: 'center',
              slots: {
                default: ({ row, column, cellValue }) => {
                  return [
                    <div>
                      <el-button
                        type="text"
                        onClick={() => this.success(row, column, false)}
                      >
                        {BTN_TEXT.HISTORICAL_DATA}
                      </el-button>
                    </div>
                  ];
                }
              }
            }
          ];

          if (this.groupType === 'WORK_SHOP') {
            this.tableColumns[0].title = '车间名称';
            this.tableColumns[0].field = 'groupName';
          } else if (this.groupType === 'PRODUCTION_LINE') {
            this.tableColumns[0].title = '产线名称';
            this.tableColumns[0].field = 'groupName';
          } else if (this.groupType === 'DEVICE') {
            this.tableColumns[0].title = '表名称';
            this.tableColumns[0].field = 'deviceName';
          }

          for (var i = 0; i < res.rows.length; i++) {
            var datax = res.rows[i].energyShiftUseVoList;
            for (var x = 0; x < datax.length; x++) {
              this.tableDatas[i]['dayEnergyConsumption' + '_' + x] = datax[x].dayEnergyConsumption;
              this.tableDatas[i]['weekConsumption' + '_' + x] = datax[x].weekConsumption;
              this.tableDatas[i]['monthConsumption' + '_' + x] = datax[x].monthConsumption;
              var json = {
                title: datax[x].shiftName,
                field: 'dayEnergyConsumption' + '_' + x,
                align: 'center'
              };
              // eslint-disable-next-line no-redeclare
              var jsonx = {
                title: datax[x].shiftName,
                field: 'weekConsumption' + '_' + x,
                align: 'center'
              };
              var jsony = {
                title: datax[x].shiftName,
                field: 'monthConsumption' + '_' + x,
                align: 'center'
              };
              if (i === 0) {
                this.tableColumns[1].children.push(json);
                this.tableColumns[2].children.push(jsonx);
                this.tableColumns[3].children.push(jsony);
              }
            }
          }
          // for (var i in res.rows) {
          //   if (this.groupType !== '') {
          //     this.tableDatas[i].name = this.tableDatas[i].groupName;
          //   } else {
          //     this.tableDatas[i].name = this.tableDatas[i].deviceName;
          //   }
          // }
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 点击确认修改
     * @param row 当前行数据
     * @param column 表头内容
     * @return: 无
     * @author: CH
     */
    success(row, column, showFlag) {
      if (this.showDialog) {
        this.showDialog(row, showFlag);
      }
    },
    /**
     * @description: 查询所属用户
     * @param 无
     * @return: 无
     * @author: ch
     */
    async exportData() {
      this.$refs.xGrid.exportData('能耗监控报表');
    //   try {
    //     const params = {
    //       type: 'GAS',
    //       groupType: this.groupType
    //     };
    //     const res = await exportEnergyMonitor(params);
    //     if (res.data.code !== undefined) {
    //       showWarningMessage(res.message);
    //     } else {
    //       var filename = '';
    //       if (this.groupType === '2') {
    //         filename = ENERGY.WORKSHOP + EXPORT_NAME.POWER_MONITORING;
    //       } else if (this.groupType === '3') {
    //         filename = ENERGY.PRODUCTION_LINE + EXPORT_NAME.POWER_MONITORING;
    //       } else {
    //         filename = ENERGY.GAS_METER + EXPORT_NAME.POWER_MONITORING;
    //       }
    //       this.$options.filters.setExportFunc(res, filename);
    //     }
    //   } catch (error) {
    //     showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
    //   } finally {
    //   }
    }
  }
};
