// 引入未审核组件
import meterTable from '@/views/energy/GasEnergyMonitoring/meterTable.vue';
import echarts from '@/views/energy/GasEnergyMonitoring/echarts.vue';
import showInfo from '@/views/energy/GasEnergyMonitoring/showInfo.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { ENERGY, STATUS_CODE, TITLE, BTN_TEXT } from '@/utils/constantsFile';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { GetEnergyConsumption } from '@/api/energymonitoring';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { configurationGetConfigurationByEnum } from '@/api/statusEquipmentReport.js';
// // 引入审核通过组件
// import waterMeter from '@/views/main/EnergyAllocation/waterMeter.vue';
// // 引入审核未通过组件
// import gasMeter from '@/views/main/EnergyAllocation/gasMeter.vue';
export default {
  name: 'GasEnergyMonitoring',
  data() {
    return {
      ENERGY: ENERGY,
      BTN_TEXT: BTN_TEXT,
      title: '',
      groupType: 'WORK_SHOP',
      hackReset: 'false',
      dayEnergyConsumption: '2311465',
      yesterdayEnergyConsumption: '564546',
      // tab默认激活下标
      active: 0,
      rows: {},
      showFlag: true,
      // 默认引入的组件名称
      currentView: 'workshop',
      dialogVisible: false,
      tabs: [
        {
          type: 'workshop',
          name: ENERGY.WORKSHOP,
          value: 'WORK_SHOP'
        },
        {
          type: 'productionLine',
          name: ENERGY.PRODUCTION_LINE,
          value: 'PRODUCTION_LINE'
        },
        {
          type: 'electricityMeter',
          name: ENERGY.GAS_NAME,
          value: 'DEVICE'
        }
      ],
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      dateTime: [],
      // 班次列表
      shiftOptions: [],
      // 班次值
      shiftData: null
    };
  },
  created() {
    this.getSystemShift();
    // 调用列表查询接口
    this.getEnergyConsumption();
  },
  methods: {
    search() {
      this.$refs.echartsRef.showEcharts();
      this.$refs.meterTable.findList();
    },
    reset() {
      this.dateTime = [];
      this.shiftData = null;
      this.$nextTick(() => {
        this.$refs.echartsRef.showEcharts();
        this.$refs.meterTable.findList();
      });
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.groupType = v;
    },
    /**
     * @description:获取今日和昨日能耗
     * @param {type}
     * @return:
     * @author: CH
     */
    async getEnergyConsumption() {
      try {
        // this.tableLoading = true;
        const res = await GetEnergyConsumption({
          type: 'GAS'
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 调用列表查询接口
            this.dayEnergyConsumption = res.rows.dayEnergyConsumption;
            this.yesterdayEnergyConsumption =
              res.rows.yesterdayEnergyConsumption;
            // 提示异常信息
            // showSuccessMessage(res.message);
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:刷新
     * @param {type}
     * @return:
     * @author: CH
     */
    Refresh() {
      this.$refs.meterTable.onTablePageChange(1, 10);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    download() {
      this.$refs.meterTable.$refs.xGrid.exportDatax();
      // this.$refs.meterTable.exportData();
    },
    /**
     * @description:打开弹窗
     * @param {type}
     * @return:
     * @author: CH
     */
    showDialog(row, showFlag) {
      this.dialogVisible = true;
      this.rows = row;
      this.showFlag = showFlag;

      this.$nextTick(() => {
        this.hackReset = true; // 重建组件
      });
      if (showFlag) {
        if (this.groupType !== '') {
          this.title = row.groupName + ENERGY.IS_INFO;
        } else {
          this.title = row.deviceName + ENERGY.IS_INFO;
        }
      } else {
        if (this.groupType !== '') {
          this.title = row.groupName + ENERGY.IS_HISTORY;
        } else {
          this.title = row.deviceName + ENERGY.IS_HISTORY;
        }
      }
    },
    handleClose() {
      this.dialogVisible = false;
      this.hackReset = false;
    },
    /**
     * @description:返回主页面
     * @param {type}
     * @return:
     * @author: CH
     */
    back() {
      this.showFlag = true;
    },
    async getSystemShift() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            var index = 0;
            for (var key in jsonArr) {
              var json = {
                value: index,
                label: key + ' ' + jsonArr[key],
                name: key,
                shiftStart: jsonArr[key].split('-')[0],
                shiftEnd: jsonArr[key].split('-')[1]
              };
              ++index;
              this.shiftOptions.push(json);
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  components: {
    // 加载未审核组件
    meterTable,
    echarts,
    HeaderTitle,
    showInfo,
    HeadOperationItem
  }
};
